* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body {
  background: linear-gradient(
    245deg,
    rgba(14, 66, 160, 1) 30%,
    rgba(0, 212, 255, 1) 100%
  );
  height: 100vh;
  overflow: hidden;
}
.container {
  max-width: 900px;
  margin: auto;
}
header {
  background: #2c64ac;
}
header nav {
  display: flex;
  flex-wrap: wrap;
  height: 50px;
}
header nav button {
  all: initial;
  padding: 10px 20px;
  text-align: center;
  cursor: pointer;
  background: #2c64ac;
  flex-grow: 1;
  text-transform: uppercase;
  color: #fff;
  border-right: 1px solid rgb(10, 41, 98);
  border-left: 1px solid rgb(10, 41, 98);
  font-family: cursive;
}
header nav button:hover {
  background: rgb(8, 32, 77);
}
header nav form {
  flex-grow: 1;
  width: 50px;
}
header nav form input {
  width: 100%;
  height: 100%;
  font-size: 18px;
  padding-left: 5px;
}

div {
  width: 100%;
  height: 100%;
}
main {
  display: grid;
  width: 100%;
  height: 100%;
  place-content: center;
  text-align: center;
  gap: 20px;
}
main #time {
  font-family: cursive;
  font-size: 80px;
  color: #fff;
}
main #time-out {
  font-family: cursive;
  font-size: 20px;
  color: #fff;
}

#hidden-one {
  display: none;
}

span {
  background: green;
  padding: 0 10px;
}
